var card = cc.Class({
    properties:{
        id : 0,
        from:"0" ,
        name:"0",
        date:"0/00/0000",
        information:"000000000",
        picUrl:'000',
        optionA:[0,0,0,0],
        optionB:[0,0,0,0],
    },
})

cc.Class({
    extends: cc.Component,

    properties: {
        cardItems:{
            default: [],
            type: card,
        },
        cardPerfab: cc.Prefab
    },

    init:function () {  
        //初始话时会初始化多张卡牌
        //和data区差不多，改变了函数名称
        for( var i=0; i<this.cardItems.length; i++ )
        {
            let item = cc.instantiate(this.cardPerfab);
            this.node.addChild(item);  //创建新节点

            let data = this.cardItems[i];
            let isTopCard = false;
            if(i==0) isTopCard = true; //顶端卡牌才显示
            item.getComponent('js_cardTemplate').init(data , isTopCard);
        }

    },

});
